<template>
<div class="main--introduction">

    <div class="scroll-button">

        <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="170px" height="40px">

            <path fill-rule="evenodd" fill="rgb(255, 255, 255)" d="M85.499,0.0 C105.514,0.0 115.325,19.99 127.94,28.999 C138.862,38.899 170.999,43.999 170.999,43.999 L0.0,43.999 C0.0,43.999 32.136,38.899 43.905,28.999 C55.673,19.99 65.485,0.0 85.499,0.0 " />

        </svg>

        <span class="arrow--icon">

            <svg id="arrow-line" data-name="arrow-line" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 18.78 10.88">

                <polyline class="arrow-line" points="0.71 0.71 9.46 9.46 18.07 0.85" style="fill:none;stroke:#c1c1c1;stroke-miterlimit:10;stroke-width:3px" />

            </svg>

        </span>

    </div>

    <p class="introduce__text">숲 아트홀은 무대와 30석의 객석, 그리고 연주회용 피아노로 <strong>‘야마하 G3 그랜드 피아노’</strong>를 보유하고 있습니다.<br v-if="onlyPC" /> 전공자들의 클래식 연주회, 2 Piano 듀오 연주회, 합주 등 다양한 공연이 가능하도록 준비되어 있습니다.</p>

    <h1 class="art-hall-title">아트홀 소개<span class="art-hall-title--line"></span></h1>

    <ul class="advantage">

        <li :class="data" v-for="(data, idx) in advantageClass" :key="idx">

            <div class="advantage--image">

                <img :src="imgSrc[idx]" :alt="advantageName[idx]">

            </div>

            <h2 class="advantage--title">

                {{ advantageName[idx] }}

            </h2>
        </li>

    </ul>

    <div class="introduction__image">

        <div class="image--container">

            <div class="image--swiper">

                <div class="image--1 image--style">
                    <div class="image--1--image image--default">
                        <img src="@/assets/img/introduction/img01.jpg" alt="piano image">
                    </div>
                </div>

                <div class="image--2 image--style" v-if="imgBoolean">

                    <div class="image--2--1">
                        <div class="image--2--1--image image--default">
                            <img src="@/assets/img/introduction/img02.jpg" alt="piano image">
                        </div>
                    </div>

                    <div class="image--2--2">
                        <div class="image--2--2--image image--default">
                            <img src="@/assets/img/introduction/img03.jpg" alt="piano image">
                        </div>
                    </div>

                </div>

                <div class="image--2 image--style" v-if="!imgBoolean">
                    <div class="image--2--image image--default">
                        <img src="@/assets/img/introduction/img02.jpg" alt="piano image">
                    </div>
                </div>

                <div class="image--3 image--style">
                    <div class="image--3--image image--default">
                        <img src="@/assets/img/introduction/img03.jpg" alt="piano image">
                    </div>
                </div>

                <div class="image--4 image--style" v-if="!imgBoolean">
                    <div class="image--4--image image--default">
                        <img src="@/assets/img/introduction/img04.jpg" alt="piano image">
                    </div>
                </div>
            </div>

        </div>

    </div>

</div>
</template>

<script>
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'Introduction',
    data: function () {
        return {
            advantageClass: ['master-class', 'anytime', '30seats'],
            advantageName: ['마스터 클래스', '24시간 사용 가능', '30석 규모의 홀 대관'],
            imgSrc: [
                require('../assets/img/introduction/icon/grand-piano.svg'),
                require('../assets/img/introduction/icon/24h.svg'),
                require('../assets/img/introduction/icon/cinema.svg'),
            ],
            onlyPC: true,
            imgBoolean: true,
        }
    },
    mounted() {
        window.addEventListener('resize', this.checkWidth);
        this.$nextTick(function () {
            window.innerWidth < 580 ? this.onlyPC = false : this.onlyPC = true;
            window.innerWidth < 1280 ? this.imgBoolean = false : this.imgBoolean = true;
        })
    },
    methods: {
        checkWidth() {
            window.innerWidth < 580 ? this.onlyPC = false : this.onlyPC = true;
            window.innerWidth < 1280 ? this.imgBoolean = false : this.imgBoolean = true;
        },
    }
}
</script>

<style lang="scss">
.scroll-button {
    position: absolute;
    width: 170px;
    top: 0;
    left: 50%;
    transform: translate(-50%, 1px);
    line-height: 0;

    img {
        display: inline-block;
    }
}

.arrow--icon {
    display: inline-block;
    position: absolute;
    width: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.arrow-line {
    stroke-dasharray: 30px;
    stroke-dashoffset: 30px;
    // animation: drawStroke 2s infinite 0s forwards;

    @keyframes drawStroke {
        0% {
            stroke-dashoffset: 30px;
        }

        50% {
            stroke-dashoffset: 0px;
        }

        100% {
            stroke-dashoffset: -30px;
        }
    }
}

.main--introduction {
    width: 100%;
    padding: 150px 0 0;
    position: relative;
}

.introduce__text {
    font-size: $fs16;
    text-align: center;
    color: #999;
    line-height: 1.75;
}

.art-hall-title {
    position: relative;
    font-size: $fs36;
    font-family: $fontB;
    text-align: center;
    padding: 100px 0 20px 0;
    color: $mainColor;

    .art-hall-title--line {
        display: inline-block;
        position: absolute;
        width: 0px;
        height: 2px;
        background: $mainColor;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        opacity: 1;
    }
}

.advantage {
    width: 1280px;
    margin: 0 auto;
    padding: 100px 20px;
    display: flex;

    li {
        text-align: center;
        width: 33.3%;
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            height: 50px;
        }
    }
}

.advantage--title {
    font-size: $fs18;
    font-family: $fontB;
    padding: 20px 0;
    color: $mainColor;
    white-space: nowrap;
}

.introduction__image {
    width: 100%;
    padding: 100px 0 150px;
    background: #f8f9fb;
}

.image--container {
    width: 1280px;
    margin: 0 auto;
    padding: 0 20px;
}

.image--swiper{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.image--style {
    width: 450px;
    height: 450px;
    border-radius: 10px;
}

.image--2 {
    width: 300px;
    margin: 0 20px;

    .image--2--1 {
        width: 100%;
        height: 215px;
        margin-bottom: 20px;
        border-radius: 10px;
    }

    .image--2--2 {
        width: 100%;
        height: 215px;
        border-radius: 10px;
    }
}

.image--default {
    width: 100%;
    height: 100%;
    background: $subColor;
    border-radius: 10px;
    overflow: hidden;
    position: relative;

    img {
        width: auto;
        height: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

@media (max-width: 1280px) {
    .advantage {
        width: 100%;
        flex-direction: column;
        align-items: center;
    }

    .anytime {
        padding: 100px 0;
    }

    .image--container {
        width: 100%;
    }

    .image--swiper{
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-content: center;
    }

    .image--style {
        width: 40%;
        flex: 1 40%;
        margin: 0;

        &:nth-of-type(2n) {
            margin-left: 10px;
        }

        &:nth-of-type(odd) {
            margin-right: 10px;
        }

        &:nth-child(n+2) {
            margin-bottom: 20px;
        }
    }
}

@media (max-width: 720px) {
    .introduce__text {
        font-size: $fs14;
    }

    .art-hall-title {
        font-size: $fs28;
    }

    .image--style {
        height: 300px;
    }
}

@media (max-width: 580px) {
    .introduce__text {
        padding: 0 20px;
        text-align: left;
    }

    .art-hall-title {
        font-size: $fs24;
    }

    .advantage {
        li {
            img {
                height: 40px;
            }
        }
    }

    .advantage--title {
        font-size: $fs16;
    }
}

@media (max-width: 480px) {
    .image--swiper{
        display: block;
        white-space: nowrap;
        position: relative;
        height: 450px;
        overflow-x: scroll;
        overflow-y: hidden;
    }

    .image--style {
        width: 90%;
        height: 100%;
        display: inline-block;
        margin: 0 20px 0 0!important;
    }

    @for $i from 1 to 5 {
        .image--#{$i} {
            // transform: translateX(if($i==1, 0, percentage(($i) - 1) + 5%));
        }
    }
}
</style>
