<template>
  <header>
      <nav class="menu">
          <a href="#" class="logo"><img src="@/assets/img/header/logo.svg" alt="forest art hall"></a>
          <span class="navigation" v-if="offsetWidth > 720">
              <a :class="data" v-for="(data, idx) in navClass" :key="idx" @click="nav">
                  {{ navName[idx] }}
              </a>
          </span>
      </nav>
  </header>
</template>

<script>
export default {
    name: 'Header',
    data: function(){
        return{
            navClass: ['introduction', 'way-to-come', 'ask'],
            navName: ['아트홀 소개', '오시는 길', '대관문의'],
            offsetWidth: 0,
        }
    },
    props: ['nav1', 'nav2', 'nav3'],
    mounted() {
        window.addEventListener('resize', this.hideNav);
        this.$nextTick(function() {
            this.offsetWidth = window.innerWidth;
        })
    },
    methods: {
        nav(event){
            this.$emit('goscroll', event.target.classList[0]);
        },
        hideNav(){
            this.offsetWidth = window.innerWidth;
        }
    }
}
</script>

<style lang="scss">
    header{
        width: 100%;
        position: fixed;
        box-shadow: 0px 5px 8px 1px rgba(50, 50, 50, 0.3);
        background: #fff;
        z-index: 1;
    }

    .menu{
        width: 1280px;
        margin: 0 auto;
        padding: 20px 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .logo{
        display: inline-block;
        line-height: 0;

        img{
            height: 24px;
        }
    }

    .navigation{
        a{
            font-size: $fs16;
            display: inline-block;
            margin-left: 50px;
            cursor: pointer;
            position: relative;

            &::before{
                display: inline-block;
                content: '';
                width: 0; height: 1px;
                position: absolute;
                background: #000;
                bottom: -3px; left: 0;
                transition: .5s;
            }
        }
    }

    @media (hover: hover){
        .navigation{
            a:hover{
                &::before{
                    width: 100%;
                }
            }
        }
    }

    @media (max-width: 1280px){
        .menu{
            width: 100%;
        }
    }

    @media (max-width: 720px){
        .menu{
            justify-content: center;
        }
    }
</style>