<template>
  <div class="map">
      <div class="map__icon">
          <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="170px" height="40px">
    
        <path fill-rule="evenodd" fill="rgb(177, 151, 124)"
    
            d="M85.499,0.0 C105.514,0.0 115.325,19.99 127.94,28.999 C138.862,38.899 170.999,43.999 170.999,43.999 L0.0,43.999 C0.0,43.999 32.136,38.899 43.905,28.999 C55.673,19.99 65.485,0.0 85.499,0.0 " />
    
    </svg>
        <span class="location--icon"><img src="@/assets/img/introduction/icon/map.svg" alt="location icon"></span>
      </div>
      <div class="map__border"></div>
      <div class="map__container">
          <div class="map__cover"></div>
      </div>
  </div>
</template>

<script>
export default {
    name: 'Map',
    data() {
        return{
            map: null,
            center: 0
        }
    },
    methods: {
        initMap() {
            const container = document.querySelector('.map__container');
            const options = {
                center: new kakao.maps.LatLng(37.666240233808416, 127.07971120707592),
                draggable: false,
                level: 1,
            };

            let map = new kakao.maps.Map(container, options);

            var infoEl = '<div class="info--title" style="padding: 10px 14px; color: #182c37;" width="100%"><h1 class="info--subtitle" style="font-size: 18px; text-align: center; font-family: Pretendard-Bold">숲 아트홀</h1><span style="display: block; width: 10%; height: 1px; background: #182c37; margin: 5px auto;"></span><p style="font-size: 14px; white-space: nowrap; text-align: center;"><span class="info--address">서울 노원구 덕릉로 112길 36 4층</span></p></div>',
            infoPosition = new kakao.maps.LatLng(37.666302260318, 127.07971668243532),
            infoRemoveable = false;

            var infoWindow = new kakao.maps.InfoWindow({
                map: map,
                position: infoPosition,
                content: infoEl,
                removeable: infoRemoveable
            });

            infoWindow;

            var infoTitle = document.querySelector('.info--title');

            infoTitle.parentElement.style.width="100%";
            infoTitle.parentElement.parentElement.style.borderRadius="5px"
            infoTitle.parentElement.previousSibling.style.transform="translateY(-2px)";


            this.map = map;
            this.center = map.getCenter();

            window.addEventListener('resize', () => {
                this.map.relayout();
                this.map.setCenter(this.center);
            })
        }
    },
    mounted() {
        if( window.kakao && window.kakao.maps ){
            this.initMap();
        } else {
            const script = document.createElement("script");

            script.onload = () => kakao.maps.load(this.initMap);
            script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=e06c0e94f18a8660bbc4171a496503bb";
            document.head.appendChild(script);
        }
    }
}
</script>

<style lang="scss">
    .map{
        width: 100%; height: 800px;
        position: relative;
        background: #f2f0e8;
    }
    
    .map__icon{
        position: absolute;
        left: 50%; top: 0;
        transform: translate(-50%, calc(-100% + 2px));
        line-height: 0;

        .location--icon{
            display: inline-block;
            position: absolute;
            width: 20px;
            left: 50%; top: 50%;
            transform: translate(-50%, -50%);

            img{
                width: 100%;
            }
        }
    }

    .map__border{
        width: 100%; height: 5px;
        background: $subColor;
        transform: translateY(1px);
    }

    .map__container{
        width: 100%; height: 100%;
        background: #9c9c9c;
    }

    .map__cover{
        width: 100%; height: 100%;
        position: absolute;
        left: 0; top: 0;
        z-index: 2;
    }
</style>